import { User, UserService, Profile } from '../core';
import { concatMap } from 'rxjs/operators/concatMap';
import { tap } from 'rxjs/operators/tap';

import { Component, OnInit, ViewChild } from '@angular/core';
import { IfscModel,GeneralModel } from '../ifsccode/ifsccode.model';
import { Service } from '../ifsccode/services/ifsccode.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Http } from '@angular/http';
import { FormControl } from '@angular/forms';
//import { Observable } from 'rxjs';
//import { map, startWith } from 'rxjs/operators';
import { Title } from '@angular/platform-browser';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import {CreateNewAutocompleteGroup, SelectedAutocompleteItem, NgAutocompleteComponent} from "ng-auto-complete";


@Component({
  selector: 'app-ifsccode',
  templateUrl: './ifsccode.component.html',
  styleUrls: ['./ifsccode.component.css'],
  providers: [
    { provide: Service, useClass: Service },    
    { provide: LocationStrategy, useClass: PathLocationStrategy }
]
})
export class IfsccodeComponent implements OnInit {
  public banks: any[];
  public states: any[];
  public districts: any[];
  public branches: any[];
  public isFind: any;
  public isParam: any;
  public filterVal: any;
  public currentLocation: any;
  public seleted: any;
  public queryBank: any;
  public queryState: any;
  public queryDistrict: any;
  public queryBranch: any;
  ifsccode: IfscModel = new IfscModel();
  general: GeneralModel = new GeneralModel();

  @ViewChild(NgAutocompleteComponent) public resBank: NgAutocompleteComponent;
  @ViewChild(NgAutocompleteComponent) public resState: NgAutocompleteComponent;
  @ViewChild(NgAutocompleteComponent) public resDistrict: NgAutocompleteComponent;
  @ViewChild(NgAutocompleteComponent) public resBranch: NgAutocompleteComponent;
    public bankGroup= [
      CreateNewAutocompleteGroup(
          'Search / choose bank',
          'resBank',
          [
              {title: '--Select--', id: ''},              
          ],
          {titleKey: 'title', childrenKey: null}
      ),
    ];
    public stateGroup= [
      CreateNewAutocompleteGroup(
          'Search / choose state',
          'resState',
          [
              {title: '--Select--', id: ''},              
          ],
          {titleKey: 'title', childrenKey: null}
      ),
    ];
    public districtGroup= [
      CreateNewAutocompleteGroup(
          'Search / choose district',
          'resDistrict',
          [
              {title: '--Select--', id: ''},              
          ],
          {titleKey: 'title', childrenKey: null}
      ),
    ];
    public branchGroup= [
      CreateNewAutocompleteGroup(
          'Search / choose branch',
          'resBranch',
          [
              {title: '--Select--', id: ''},              
          ],
          {titleKey: 'title', childrenKey: null}
      ),
    ];
    // constructor(){}
    constructor(private _service: Service, private _Route: Router, private activatedRoute: ActivatedRoute, private locationService: Location) {            
      
      //this.currentLocation = window.location.href;
      this.currentLocation = _Route.url;
      //this.currentLocation = 'http://localhost:4200/';
      
      this.isParam = false;
      this.seleted = false;
      
    }
    // Selected(item: SelectedAutocompleteItem) {
    //   alert(item);
    // }
    // constructor(private _service: Service, private _Route: Router, private activatedRoute: ActivatedRoute, private titleService: Title, private locationService: Location, private platformStrategy: LocationStrategy) {            
    //   debugger;      
    //   this.currentLocation = _Route.url;
    //   this.isParam = false;
    //   this.seleted = false;
    //   let t = titleService.getTitle();
    // }

  setDropdownFromUrl(): void {

    this.queryBank = this.activatedRoute.snapshot.paramMap.get('bankId');
    this.queryState = this.activatedRoute.snapshot.paramMap.get('stateId');
    this.queryDistrict = this.activatedRoute.snapshot.paramMap.get('districtId');
    this.queryBranch = this.activatedRoute.snapshot.paramMap.get('branchId');

    if (this.queryBank != null) {
        this.general.bankName = this.queryBank.split('-')[0];
        this.general.bankId = this.queryBank.split('-')[1];
        this.isParam = true;       
        this.filterStates("");
        if (this.queryState != null) {
            this.general.stateName = this.queryState.split('-')[0];
            this.general.stateId = this.queryState.split('-')[1];
            this.filterDistrict("");
            if (this.queryDistrict != null) {
                this.general.districtName = this.queryDistrict.split('-')[0];
                this.general.districtId = this.queryDistrict.split('-')[1];
                this.filterBranch("");
                if (this.queryBranch != null) {
                    this.general.branchName = this.queryBranch.split('-')[0];
                    this.general.branchId = this.queryBranch.split('-')[1];
                }
            }
        }
    }
}
ngOnInit(): void {
    //this.pService.start();
    this._service
        .GetAllBanks()
        .subscribe(result => {
            this.isFind = false;
            this.banks = result.data as any[];
            this.setDropdownFromUrl();
            
            if(this.isParam)
            {
                this.bankGroup = [
                    CreateNewAutocompleteGroup(
                       this.general.bankName,
                        'resBank',
                        this.banks,
                        {titleKey: 'name', childrenKey: null}
                    ),
                  ];
            }
            else{this.bankGroup = [
                CreateNewAutocompleteGroup(
                    'Search / choose bank',
                    'resBank',
                    this.banks,
                    {titleKey: 'name', childrenKey: null}
                ),
              ];
            }
            
            
            //this.pService.done();
        },
            error => {
                if (error) {
                    alert("An Error has occured please try again after some time !");
                }
            });

}
//onchange bank dropdown
  filterStates(filter: any) {   
    //this.pService.start();
    if (filter != "") {
      
        this.filterVal =filter.item.id;        
        var optionText = filter.item.title;
        
        this.general.bankId = this.filterVal;
        this.general.bankName = optionText;
        
        if (this.queryBank != null) {
            this.currentLocation = "/ifsccode";
        }
        if (!this.seleted) {
            this.seleted = true;
        }
        else {
            if (this.queryBank != null) {
                this.currentLocation = "/ifsccode";
            }
            else {
                this.currentLocation = this._Route.url;
            }
        }
        this.currentLocation = this.currentLocation + "/" + optionText + "-" + this.filterVal;
        this.locationService.replaceState(this.currentLocation);

    }
    else {
        this.filterVal = this.general.bankId;
    }


    this._service
        .GetStatesByBankId(this.filterVal)
        .subscribe(result => {
            this.states = result.data as any[];
           
            if(this.isParam && this.general.stateName!=undefined && this.general.stateName!='')
            {
                this.stateGroup = [
                    CreateNewAutocompleteGroup(
                      this.general.stateName,
                        'resState',
                        this.states,
                        {titleKey: 'name', childrenKey: null}
                    ),
                  ];
            }
            else{
                this.stateGroup = [
                    CreateNewAutocompleteGroup(
                      'Search / choose state',
                        'resState',
                        this.states,
                        {titleKey: 'name', childrenKey: null}
                    ),
                  ];
            }
            
            //this.pService.done();
        },
            error => {
                if (error) {
                    alert("An Error has occured please try again after some time !");
                }
            });
    this.isFind = false;
    if (!this.isParam || this.seleted ) {
        this.general.stateId = 0;
        this.general.stateName = "";
        this.filterDistrict(0);
    }

    //this.pService.done();
}

//onchange state dropdown
filterDistrict(filter: any) {
    //this.pService.start();

    if (filter != "" && filter != "0") {
      this.filterVal =filter.item.id;        
      var optionText = filter.item.title;
      
      this.general.stateId = this.filterVal;
      this.general.stateName = optionText;
                   

        if (this.queryState != null) {
            this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId;
        }
        if (!this.seleted) {
            this.seleted = true;
        }
        else {
            if (this.queryState != null) {
                this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId;
            }
            else {
                this.currentLocation = this._Route.url + "/" + this.general.bankName + "-" + this.general.bankId;
            }
        }
        this.currentLocation = this.currentLocation + "/" + optionText + "-" + this.filterVal;
        this.locationService.replaceState(this.currentLocation);
    }
    else {
        this.filterVal = this.general.stateId;
    }

    if (this.filterVal != '' || this.filterVal == "0") {
        this._service
            .GetDistrictByStateId(this.filterVal)
            .subscribe(result => {
                this.districts = result.data as any[];
                if(this.isParam && this.general.districtName!=undefined && this.general.districtName!='')
                {
                this.districtGroup = [
                    CreateNewAutocompleteGroup(
                        this.general.districtName,
                        'resDistrict',
                        this.districts,
                        {titleKey: 'name', childrenKey: null}
                    ),
                  ];
                }
                else{
                    this.districtGroup = [
                    CreateNewAutocompleteGroup(
                        'Search / choose District',
                        'resDistrict',
                        this.districts,
                        {titleKey: 'name', childrenKey: null}
                    ),
                  ];
                }
                
            },
                error => {
                    if (error) {
                        alert("An Error has occured please try again after some time !");
                    }
                });
    }
    this.isFind = false;
    if (!this.isParam || this.seleted) {
        this.general.districtId = 0;
        this.general.districtName = "";
        this.filterBranch(0);
    }
    //this.pService.done();
}
//onchange district dropdown
filterBranch(filter: any) {
    //this.pService.start();
    if (filter != "" && filter != "0") {
        this.filterVal =filter.item.id;        
        var optionText = filter.item.title;
        
        this.general.districtId = this.filterVal;
        this.general.districtName = optionText;           

        if (this.queryDistrict != null) {
            this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId;
        }
        if (!this.seleted) {
            this.seleted = true;
        }
        else {
            if (this.queryDistrict != null) {
                this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId;
            }
            else {
                this.currentLocation = this._Route.url + "/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId;
            }
        }
        this.currentLocation = this.currentLocation + "/" + optionText + "-" + this.filterVal;
        this.locationService.replaceState(this.currentLocation);
    }
    else {
        this.filterVal = this.general.districtId;
    }
    if (this.filterVal != '' || this.filterVal == "0") {
        this._service
            .GetBranchByDistrictId_BankId(this.filterVal, this.general.bankId)
            .subscribe(result => {
                this.branches = result.data as any[];
                if(this.isParam && this.general.branchName!=undefined && this.general.districtName!='')
                {
                    this.branchGroup = [
                        CreateNewAutocompleteGroup(
                           this.general.branchName,
                            'resBranch',
                            this.branches,
                            {titleKey: 'name', childrenKey: null}
                        ),
                      ];
                }
                else{
                    this.branchGroup = [
                        CreateNewAutocompleteGroup(
                            'Search / choose Branch',
                            'resBranch',
                            this.branches,
                            {titleKey: 'name', childrenKey: null}
                        ),
                      ];
                }
                
            },
                error => {
                    if (error) {
                        alert("An Error has occured please try again after some time !");
                    }
                });
    }
    this.isFind = false;
    //this.pService.done();
}
//onchange district dropdown
onBranchSelection(filter: any) {
    this.isFind = false;
    this.filterVal =filter.item.id;        
    var optionText = filter.item.title;        
    this.general.branchId = this.filterVal;
    this.general.branchName = optionText;
    if (this.queryBranch != null) {
        this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId + "/" + this.general.districtName + "-" + this.general.districtId;
    }
    if (!this.seleted) {
        this.seleted = true;
    }
    else {
        if (this.queryBranch != null) {
            this.currentLocation = "/ifsccode/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId + "/" + this.general.districtName + "-" + this.general.districtId;
        }
        else {
            this.currentLocation = this._Route.url + "/" + this.general.bankName + "-" + this.general.bankId + "/" + this.general.stateName + "-" + this.general.stateId + "/" + this.general.districtName + "-" + this.general.districtId;
        }
    }

    this.currentLocation = this.currentLocation + "/" + optionText + "-" + this.filterVal;
    this.locationService.replaceState(this.currentLocation);

}

//Find IFSC Code
findIFSCCode() {
    //this.pService.start();
    this._service
        .findIFSCCode(this.general.branchId)
        .subscribe(result => {
            this.isFind = true;
            this.ifsccode = result.data as IfscModel;
            //this.pService.done();
        },
            error => {
                if (error) {
                    alert("An Error has occured please try again after some time !");
                }
            });
}

}



