import { Injectable,Inject } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { catchError } from 'rxjs/operators';

// import 'rxjs/add/operator/map';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/observable/throw';

@Injectable()
export class Service {
    private actionUrl: string;   
    private actionUrlGet: string;   
    private apiBaseUrl: string;   

    constructor(private _http: Http) {

        //this.apiBaseUrl = baseUrl.toString();
        
        this.apiBaseUrl = 'http://localhost:59126/api/';
        
        this.actionUrl = '';
        //this.actionUrl = 'http://localhost:59126/api/Bank';        
        this.actionUrlGet = '';
    }

    public GetAllBanks = (): Observable<any> => {   
        this.actionUrl = this.apiBaseUrl +'Bank';
      
        return this._http.get(this.actionUrl).pipe(
            map((response: Response) => <any>response.json()),
            catchError(response => {
                alert(response.status);               
                return response;
            }));         
    
    }
    public GetStatesByBankId = (bankId: any): Observable<any> => {
        bankId = bankId == '' || bankId == undefined ? 0 : bankId;
        this.actionUrlGet = this.apiBaseUrl +'State/'+bankId;
        return this._http.get(this.actionUrlGet).pipe(
            map((response: Response) => <any>response.json()),
            catchError(response => {
                alert(response.status);               
                return response;
            }));         

    }

    public GetDistrictByStateId = (stateId: any): Observable<any> => {        
        stateId = stateId == '' || stateId == undefined ? 0 : stateId;
        this.actionUrlGet = this.apiBaseUrl +'District/' + stateId;
        return this._http.get(this.actionUrlGet).pipe(
            map((response: Response) => <any>response.json()),
            catchError(response => {
                alert(response.status);               
                return response;
            }));         

    }
    public GetBranchByDistrictId_BankId = (districtId: any, bankId: any): Observable<any> => {
        bankId = bankId == '' || bankId == undefined  ? 0 : bankId;
        districtId = districtId == '' || districtId == undefined ? 0 : districtId;
        this.actionUrlGet = this.apiBaseUrl +'Branch/' + districtId + '/' + bankId;
        return this._http.get(this.actionUrlGet).pipe(
            map((response: Response) => <any>response.json()),
            catchError(response => {
                alert(response.status);               
                return response;
            }));         

    }

    public findIFSCCode = (branchId: any): Observable<any> => {        
        this.actionUrlGet = this.apiBaseUrl +'IFSC/' + branchId;
        return this._http.get(this.actionUrlGet).pipe(
            map((response: Response) => <any>response.json()),
            catchError(response => {
                alert(response.status);               
                return response;
            }));         

    }
}


