import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import {IfsccodeComponent} from './ifsccode/ifsccode.component';
const routes: Routes = [
  {
    path: 'settings',
    loadChildren: './settings/settings.module#SettingsModule'
  },
  {
    path: 'profile',
    loadChildren: './profile/profile.module#ProfileModule'
  },
  {
    path: 'editor',
    loadChildren: './editor/editor.module#EditorModule'
  },
  {
    path: 'article',
    loadChildren: './article/article.module#ArticleModule'
  } 
  ,
  {
    path: 'ifsccode',
    //loadChildren: './article/article.module#ArticleModule'
    component:IfsccodeComponent
    
//    loadChildren: './ifsccode/ifsccode.module#IfscModule'
  },
  { path: 'ifsccode/:bankId', component: IfsccodeComponent },
  { path: 'ifsccode/:bankId/:stateId', component: IfsccodeComponent },
  { path: 'ifsccode/:bankId/:stateId/:districtId', component: IfsccodeComponent },
  { path: 'ifsccode/:bankId/:stateId/:districtId/:branchId', component: IfsccodeComponent },
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    // preload all modules; optionally we could
    // implement a custom preloading strategy for just some
    // of the modules (PRs welcome 😉)
    preloadingStrategy: PreloadAllModules
  })],
  exports: [RouterModule]
})
export class AppRoutingModule {}



